import React from 'react';
import styled from 'styled-components';
import {InnerLayout} from '../Styles/Layout';
import Title from '../Components/Title';
import SmallTitle from '../Components/SmallTitle';
import BusinessCenterIcon from '@material-ui/icons/BusinessCenter';
import SchoolIcon from '@material-ui/icons/School';
import ResumeItem from '../Components/ResumeItem';

function Resume() {
    const briefcase = <BusinessCenterIcon />
    const school = <SchoolIcon />
    return (
        <ResumeStyled>
            <Title title={'Resume'} span={'resume'} />
            <InnerLayout>
                <div className="small-title">
                    <SmallTitle icon={briefcase} title={'Working Experience'} />
                </div>
                <div className="resume-content">
                    <ResumeItem 
                        year={'2021 - Present'} 
                        title={'Graduate Research Assistant'}
                        subTitle={'University of Texas at San Antonio'}
                        text={'Working to develop an algorithm that extracts data from twitter and analyze it to show trends on daily level for different cities in United States. Developing a robust and flexible algorithm which extracts daily data using Twython from twitter. Making use of NumPy, Pandas, NLTK and techniques such as Lemmatization and Stopwords for data cleaning. Using VADER to analyze sentiments and also coded LIWC word count algorithm. Visualizing daily and hourly patterns using Plotly and Matplotlib'} 
                    />
                    <ResumeItem 
                        year={'2020 - 2021'} 
                        title={'Full Stack Developer Intern'}
                        subTitle={'Dimensionless Tech'}
                        text={'Working with the team to design modern and reusable UI using web component design and robust backend API’s using MVT architecture. Developed efficient and easy to use UI for Baggage AI, Counterfeit AI using Node.js, React.JS and Bootstrap. Developed robust backend API’s using Python Django Framework and tested it using Postman. Created a complete procedure to convert web applications into portable kiosks using Mir-Kiosk and Snapcraft. Managed and Updated Baggage AI website which uses Laravel (PHP) as primary framework '} 
                    />

                    <ResumeItem 
                        year={'2020 - 2020'} 
                        title={'Natural Language Processing Intern'}
                        subTitle={'Suven Consultants and Technologies Pvt Ltd'}
                        text={'Developed a system to analyze sentiments from textual documents. Applied various concepts like Stemming, lemmatization etc. Coded the system in 5 different algorithms which were AFINN, SentiWordNet, VADER, Logistic regression and Support Vector Machine (SVM). '} 
                    />

                    <ResumeItem 
                        year={'2019 - 2019'} 
                        title={'Management Intern'}
                        subTitle={'Metro Motors'}
                        text={' I was in charge of maintaining and managing the digital Systems and Inventory. Ordering motor parts and keeping track of ordered items. Managed the finance and the budget of the company for the given period '} 
                    />
                    <ResumeItem 
                        year={'2018 - 2018'} 
                        title={'Web Developer Intern'}
                        subTitle={'ITdeation Technologies LLP'}
                        text={'Developed dynamic webpages using JavaScript as per client specifications and requirements.Daily programming in server and client-side by using JavaScript and other web technologies. Developed web pages that are dynamic and which satisfy clients requirement. Involved in planning and architecting the website '} 
                    />

                
                  
                </div>
                <div className="small-title u-small-title-margin">
                    <SmallTitle icon={school} title={'Educational Qualifications'} />
                </div>
                <div className="resume-content ">
                    <ResumeItem 
                        year={'2021 - Present'} 
                        title={'Masters in Computer Science'}
                        subTitle={'University of Texas at San Antonio'}
                        text={'Pursuing my masters in CS with a concentration in Data Science.Some of the relevant courses are Analysis of Algorithms, Data Mining, Software Testing and Quality, Programming languages and Compiler, Operating Systems '} 
                    />
                    <ResumeItem 
                        year={'2016 - 2020'} 
                        title={'Bachelors of Engineering in Information Technology'}
                        subTitle={'Shree L.R Tiwari College of Engineering, Mumbai University'}
                        text={'Completed my undergrad degree with a specialization in Information Technology in 2020. Some of the relevant courses include Data Structures & Analysis, Software Engineering & Project Management, Cloud Computing, Advance Database Management Technology, Object Oriented Programming(Java), Artificial Intelligence, DBMS.'} 
                    />
                    <ResumeItem 
                        year={'2014 - 2016'} 
                        title={'HSC 12TH Grade'}
                        subTitle={'Royal College of Arts, Science and Commerce'}
                        text={'Completed my HSC in the field of Science (Physics,Chemistry,Mathematics) and Biology as a concentration. '} 
                    />
                </div>
            </InnerLayout>
        </ResumeStyled>    
    )
}

const ResumeStyled = styled.section`
    .small-title{
        padding-bottom: 3rem;
    }
    .u-small-title-margin{
        margin-top: 4rem;
    }
    .resume-content{
        border-left: 2px solid var(--border-color);
    }
`;
export default Resume