import React from 'react';
import styled from 'styled-components';
import {InnerLayout} from "./../Styles/Layout";
import Title from "./../Components/Title";
import ServiceCard from "./../Components/ServiceCard";
import design from "./../img/design.svg";
import intelligence from "./../img/intelligence.svg";
import gamedev from "./../img/game-dev.svg";

function Services() {
    return (
        <InnerLayout>
            <Servicesstyled>
                <Title title={'Servives'} span={'Services'} />
                <div className="services">
                    <ServiceCard image={design}
                     title={'Web Development'}
                     paragraph={' I design websites and webapps. Have strong experience with technologies like HTML, CSS, Javascript, ReactJS, NodeJS, Django etc'} />

            

            <div className="mid-card">
                <ServiceCard image={intelligence}
                     title={'Data Science'}
                     paragraph={' I create data science/Machine Learning projects. Have strong experience using diverse ML/DL Algorithms and packages.'} />
            </div>
            <ServiceCard image={gamedev}
                     title={'Software Development'}
                     paragraph={' I develop robust algorithms and Restfull APIS for backend. I can also develop desktop applications and programs to automate your tasks.'} />
                </div>
            </Servicesstyled>
        </InnerLayout>
    )
}

const Servicesstyled = styled.div`

.services{
    margin-top: 5rem;
    display: flex;
    justify-content: space-between;

    .mid-card{
        margin: 0 1.2rem;
    }
}

`;

export default Services
