import React from 'react';
import styled from 'styled-components';
import { InnerLayout } from '../Styles/Layout';
import ReviewItem from './ReviewItem';
import Title from './Title';


function Review() {
    return (

            <Reviewstyled>
                <Title title={'Reviews'} span={'Reviews'} />
                <InnerLayout>
                    <div className="reviews">
                        <ReviewItem text={'Shakib is very flexible to work with. Accepts challenges and completes given projects/tasks on time.'}/>
                        <ReviewItem text={'Shakib is a very proficient software Developer. Can brainstorm on your problems and finds the perfect software solution for you'}/>
                        
                    </div>
                </InnerLayout>
            </Reviewstyled>
       
        )
        
        
}

const Reviewstyled = styled.div`
.reviews{
    display: flex;
}
`;


export default Review;
